/*============================*/

@import url('https://fonts.googleapis.com/css?family=Rubik:300,300i,400,400i,500,500i,700,700i,900,900i&display=swap&subset=cyrillic,hebrew,latin-ext');


html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}
body {
  background-color: hsl(0, 0%, 16%);
  font-family: "Rubik";
  font-size: 16px;
}
div {
  display: block;
}



.v-bar {
 width: 6px;
 height: auto;
 border-radius: 6px;
 display: block;
}


/*== COLOR SECTION ================================*/
.white {
  background-color: #fff;
}

.color1 {
  background-color: #d32f2f;
}

.color2 {
  background-color: #388e3c;
}

.color3 {
  background-color: #303f9f;
}


/*== HEADER SECTION ================================*/
header {
  width: 100%;
  height: 80px;
  align-items: center;
  background-color: hsl(0, 0%, 12%);
  display: flex;
  flex: none;
  position: relative;
  transition: 0.2s;
}
header:hover {
  background-color: hsl(0, 0%, 12%);
}
.header {
  width: calc(100% - 64px);
  max-width: 1024px;
  max-height: 80px;
  margin: auto;
  display: flex;
  position: relative;
}

.logo {
  display: flex;
  align-items: center;
  position: relative;
  text-decoration: none;
  color: hsl(0, 0%, 100%);
}
.logo img {
  width: 64px;
  height: 64px;
  filter: invert(100%);
  -webkit-filter: invert(100%);
}
.logo-text {
  font-family: "Rubik";
  font-weight: 800;
  font-size: 20px;
  text-decoration: none;
  color: hsl(0, 0%, 100%);
  margin: 0px 32px 0 8px;
  align-items: center;
  visibility: visible;
}
nav {
  margin-left: auto;
  align-items: center;
  display: -webkit-box;
	display: -ms-flexbox;
  display: flex;
  flex: none;
  position: relative;
}
.nm {
  padding: 8px 16px;
  margin: 4px;
  background-color: hsla(0, 0%, 12%, 0);
  font-weight: 600;
  font-size: 14px;
  text-decoration: none;
  color: #fff;
  border-radius: 6px;
  opacity: 1;
  transform-origin: right;
  transition: 0.2s;
}
header:hover .nm {
  opacity: 0.4;
}
nav .nm:hover {
  opacity: 1;
}
.nm:nth-child(1):hover {
  background-color: #d32f2f;
}
.nm:nth-child(2):hover {
  background-color: #388e3c;
}
.nm:nth-child(3):hover {
  background-color: #303f9f;
}
#navbar {
  overflow: hidden;
  z-index: 1;
}
.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}
.sticky + .content {
  padding-top: 80px;
}


/*== CONTAINER SECTION ================================*/
.container {
  width: 100%;
  height: 200px;
}
.container-bg {
  background-image: url(./img/fairu-zu_project_X7_RGB.jpg);
  background-size: cover;
  background-position: 50% 30%;
  height: calc(100% + 40px);
}
.blackout {
  background-color: hsla(0, 0%, 12%, 0.75);
  height: 100%;
}
.container-content {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
h1 {
  margin: 0 32px;
  text-align: center;
  text-shadow: 0 0 2px hsla(0, 0%, 12%, 0.75);
  font-size: 54px;
  letter-spacing: 12px;
  color: #fff;
  display: block;
}


/*== MAIN CONTENT SECTION =============================*/
.main {
  width: calc(100% - 64px);
  max-width: 1024px;
  margin: auto;
  padding-bottom: 32px;
  background-color: hsl(0, 0%, 14%);
}
.main-border {
  width: 100%;
  height: 4px;
  margin: auto;
  background-color: hsl(0, 0%, 100%);
  border-radius: 16px 16px 0 0;
  display: block;
}
.featured-title {
  margin: 32px 0 32px 32px;
  display: flex;
}
h2 {
  width: auto;
  margin: 0 16px;
  font-size: 32px;
  color: hsl(0, 0%, 100%);
  display: block;
}


/*== IMAGE GALLERY =============================*/
.img-container {
  margin: 32px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 8px;
}
.img-container div {
  position: relative;
  background: hsl(0, 0%, 12%);
  border-radius: 8px;
}
.img-container div img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}
.img-h {
  grid-row: span 2;
  grid-column: span 3;
}
.img-hl {
  grid-row: span 2;
  grid-column: span 4;
}
.img-v {
  grid-row: span 4;
  grid-column: span 3;
}
.img-vs {
  grid-row: span 3;
  grid-column: span 2;
}
.img-box{
  grid-row: span 2;
  grid-column: span 2;
}


/*== FOOTER SECTION =============================*/
footer {
  width: 100%;
  height: 80px;
  margin-top: 16px;
  background-color: hsl(0, 0%, 12%);
}
